<template>
  <div class="fk">
    <headers :title="title"></headers>
    <div class="top">
      <div class="userMsg">
        <img class="icon1" src="../assets/fk/icon1.png" alt="">
        <div class="msg">
          <p class="user">
            <span>收货人：<i>郭靖</i></span>
            <span>13388888888</span>
          </p>
          <p class="address">收货地址：重庆重庆市渝北区回兴街道渝北西政校区</p>
          <p class="tishi">(收货不便时，可选择免费代收货服务)</p>
        </div>
        <img class="icon2" src="../assets/fk/icon2.png" alt="">
      </div>
      <div class="sp">
        <p><span>商品金额</span><span>￥89.90</span></p>
        <p><span>总运费<i>（24小时内发货）</i></span><span>￥0.00</span></p>
      </div>
    </div>
    <div class="btn">
      <span></span>
      <van-switch v-model="checked" />
    </div>
    <div class="zffs">
      <h3>支付方式</h3>
      <div class="zf">
        <p @click="set('1')">
          <img src="../assets/fk/icon.png" alt="">
          <span>支付宝</span>
          <van-tag plain type="danger">推荐</van-tag>
        </p>
        <van-radio name="1" v-model="radio"></van-radio>
      </div>
      <div class="zf">
        <p @click="set('2')">
          <img src="../assets/fk/weixin.png" alt="">
          <span>微信</span>
          <van-tag plain type="danger">推荐</van-tag>
        </p>
        <van-radio name="2" v-model="radio"></van-radio>
      </div>
      <div class="zf">
        <p @click="set('3')">
          <img src="../assets/fk/ywt.png" alt="">
          <span>一网通支付</span>
        </p>
        <van-radio name="3" v-model="radio"></van-radio>
      </div>
    </div>
    <a href="#">立即付款</a>
  </div>
</template>

<style lang='less' scoped>
  .fk {
    background: #f6f6f6;
    flex: 1;
    font-family: 'PingFang-SC-Medium';
    display: flex;
    flex-direction: column;

    .top {
      background: #fff;
      margin-top: 90px;
      padding: 0 30px;
      height: 346px;
      .userMsg {
        height: 192px;
        border-bottom: 1px solid #e5e5e5;
        display: flex;
        justify-content: space-between;
        align-items: center;
        .icon1 {
          width: 36px;
          height: 72px;
        }
        .msg {
          flex: 1;
          padding-left: 24px;
          .user {
            display: flex;
            justify-content: space-between;
            font-size: 28px;
            i {
              font-size: 32px;
              font-style: normal;
            }
          }
          .address {
            font-size: 26px;
            padding: 20px 32px 20px 0;
          }
          .tishi {
            font-size: 24px;
            color: #e9a945;
          }
        }
        .icon2 {
          width: 18px;
          height: 60px;
        }
      }
      .sp {
        height: 154px;
        p {
          height: 50%;
          display: flex;
          justify-content: space-between;
          align-items: center;
          font-size: 24px;
          color: #000;
          i {
            color: #969696;
          }
        }
      }
    }

    .btn{
      height: 92px;
      background: #fff;
      margin: 26px 0;
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 0 30px;
    }

    .zffs{
      padding: 0 30px;
      background: #fff;
      h3{
        font-size: 24px;
        color: #000;
        height: 90px;
        line-height: 90px;
        border-bottom: 1px solid #e5e5e5;
        margin-bottom: 70px;
      }
      .zf{
        display: flex;
        justify-content: space-between;
        font-size: 30px;
        align-items: center;
        margin-bottom: 96px;
        img{
          width: 70px;
          margin-right: 46px;
        }
        span{
          margin-right: 38px;
        }
      }
    }

    a{
      height: 98px;
      background: #653a72;
      color: #fff;
      line-height: 98px;
      text-align: center;
      font-size: 34px;
    }
  }
</style>

<script>
  import "lib-flexible"
  import headers from '../components/editHeader.vue'
  export default {
    components: {
      headers
    },
    data() {
      return {
        title: "付款",
        checked: true,
        radio: '1'
      }
    },
    methods: {
      set: function (index) {
        this.radio = index
      }
    }
  }
</script>



